:root {
    --color-gray: #545454;
    --violet: rebeccapurple;
}

body {
    background: red;
    color: var(--color-gray);


    p {
        color: var(--violet);
    }

    .gh-head {
        background: cadetblue;
        position: fixed;
        width: 100%;
        left: 0;
        top: 0;
        right: 0;
        box-shadow: 0 10px 10px rgba(black, 0.2);
    }
}
